@import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
}

#app {
  height: 100%;
  min-height: 100vh;
}

body{
  font-family: 'Lato', sans-serif;
  background-color: #ffffff;
  color: #232C33;
  height: 100%;
}



h2{
  color: white;
  padding: 10px;
  text-shadow: -2px -2px 0 #000, 2px -2px 0 #000, -2px 2px 0 #000, 2px 2px 0 #000;
}

header {
  text-transform: uppercase;
  font-size: 1.4em;
  color: #ffffff;
  text-align: center;
  padding: 30px;
  margin: 0;
  background-color: #232C33;
}

.restaurant-header{
  background-color: #232C33;
  text-align: center;
}



.container{
  background-color: #F3C178;
  flex-grow: 1;
  flex-basis: auto;
  margin: 0.5rem;
  width: 400px;
  max-width: 100%;
  padding-bottom: 20px;
  box-shadow: 2px 2px 5px grey;
}

.flex{
  margin: 10px 0;
  display: flex;
  flex-wrap: wrap;
}


.subheader{
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #264653;
  margin: 20px 20px 10px;
}

.components{

  margin-left: 20px;
  margin-right: 20px;
}





footer{
  color: #ffffff;
  background-color: #264653;
  text-transform: uppercase;
  font-size: 12px;
  padding: 10px;
}